  getFilteredData(data) {
    let filtered = [...data];
    if (this.filters.status !== 'all') {
      filtered = filtered.filter(item => item.status === this.filters.status);
    }
    if (this.filters.category !== 'all') {
      filtered = filtered.filter(item => item.category === this.filters.category);
    }
    if (this.filters.type !== 'all') {
      filtered = filtered.filter(item => item.type === this.filters.type);
    }
    if (this.filters.search) {
      const search = this.filters.search.toLowerCase();
      filtered = filtered.filter(
        item =>
          (item.title && item.title.toLowerCase().includes(search)) ||
          (item.name && item.name.toLowerCase().includes(search))
      );
    }
    return filtered;
  }

  getDay(date) {
    if (!date) return '--';
    return new Date(date).getDate();
  }

  getMonth(date) {
    if (!date) return '--';
    const months = [
      'ŸäŸÜÿßŸäÿ±',
      'ŸÅÿ®ÿ±ÿßŸäÿ±',
      'ŸÖÿßÿ±ÿ≥',
      'ÿ£ÿ®ÿ±ŸäŸÑ',
      'ŸÖÿßŸäŸà',
      'ŸäŸàŸÜŸäŸà',
      'ŸäŸàŸÑŸäŸà',
      'ÿ£ÿ∫ÿ≥ÿ∑ÿ≥',
      'ÿ≥ÿ®ÿ™ŸÖÿ®ÿ±',
      'ÿ£ŸÉÿ™Ÿàÿ®ÿ±',
      'ŸÜŸàŸÅŸÖÿ®ÿ±',
      'ÿØŸäÿ≥ŸÖÿ®ÿ±',
    ];
    return months[new Date(date).getMonth()];
  }

  async loadData() {
    if (!this.useAPI) {
      this.loadFromLocalStorage();
      return;
    }

    try {
      if (!this.connectionManager.isFullyConnected()) {
        this.loadFromLocalStorage();
        return;
      }

      const [events, activities, registrations, attendees, speakers, sponsors, venues, analytics] =
        await Promise.all([
          this.apiClient.get(this.API_ENDPOINTS.events || '/api/advanced-events/events'),
          this.apiClient.get(this.API_ENDPOINTS.activities || '/api/advanced-events/activities'),
          this.apiClient.get(
            this.API_ENDPOINTS.registrations || '/api/advanced-events/registrations'
          ),
          this.apiClient.get(this.API_ENDPOINTS.attendees || '/api/advanced-events/attendees'),
          this.apiClient.get(this.API_ENDPOINTS.speakers || '/api/advanced-events/speakers'),
          this.apiClient.get(this.API_ENDPOINTS.sponsors || '/api/advanced-events/sponsors'),
          this.apiClient.get(this.API_ENDPOINTS.venues || '/api/advanced-events/venues'),
          this.apiClient.get(this.API_ENDPOINTS.analytics || '/api/advanced-events/analytics'),
        ]);

      this.events = events.data || [];
      this.activities = activities.data || [];
      this.registrations = registrations.data || [];
      this.attendees = attendees.data || [];
      this.speakers = speakers.data || [];
      this.sponsors = sponsors.data || [];
      this.venues = venues.data || [];
      this.analytics = analytics.data || [];

      this.saveToLocalStorage();
      this.updateContent();
    } catch (error) {
      console.error('Error loading events data:', error);
      this.loadFromLocalStorage();
    }
  }

  setupRealtimeSync() {
    if (!this.realtimeSync) return;
    this.realtimeSync.subscribe('advanced-events', '*', update => {
      if (update.action === 'create' || update.action === 'update' || update.action === 'delete') {
        this.loadData();
      }
    });
  }

  setupConnectionMonitoring() {
    if (!this.connectionManager) return;
    this.connectionManager.on('online', () => {
      this.loadData();
    });
  }

  switchView(view) {
    this.currentView = view;
    this.updateContent();
  }

  handleFilterChange(type, event) {
    this.filters[type] = event.target.value;
    this.updateContent();
  }

  handleSearch(event) {
    this.filters.search = event.target.value;
    this.updateContent();
  }

  updateContent() {
    const content = document.getElementById('eventsContent');
    if (content) {
      content.innerHTML = this.renderCurrentView();
    }
  }

