  formatCurrency(amount) {
    return new Intl.NumberFormat('ar-SA', {
      style: 'currency',
      currency: 'SAR',
      minimumFractionDigits: 2,
    }).format(amount);
  }

  formatDate(date) {
    if (!date) return 'ÿ∫Ÿäÿ± ŸÖÿ≠ÿØÿØ';
    return new Date(date).toLocaleDateString('ar-SA');
  }

  formatDateTime(dateTime) {
    if (!dateTime) return 'ÿ∫Ÿäÿ± ŸÖÿ≠ÿØÿØ';
    return new Date(dateTime).toLocaleString('ar-SA');
  }

  saveToLocalStorage() {
    try {
      localStorage.setItem('advancedEvents', JSON.stringify(this.events));
      localStorage.setItem('advancedActivities', JSON.stringify(this.activities));
      localStorage.setItem('advancedRegistrations', JSON.stringify(this.registrations));
      localStorage.setItem('advancedAttendees', JSON.stringify(this.attendees));
      localStorage.setItem('advancedSpeakers', JSON.stringify(this.speakers));
      localStorage.setItem('advancedSponsors', JSON.stringify(this.sponsors));
      localStorage.setItem('advancedVenues', JSON.stringify(this.venues));
      localStorage.setItem('advancedAnalytics', JSON.stringify(this.analytics));
    } catch (error) {
      console.error('Error saving to localStorage:', error);
    }
  }

  loadFromLocalStorage() {
    try {
      this.events = JSON.parse(localStorage.getItem('advancedEvents') || '[]');
      this.activities = JSON.parse(localStorage.getItem('advancedActivities') || '[]');
      this.registrations = JSON.parse(localStorage.getItem('advancedRegistrations') || '[]');
      this.attendees = JSON.parse(localStorage.getItem('advancedAttendees') || '[]');
      this.speakers = JSON.parse(localStorage.getItem('advancedSpeakers') || '[]');
      this.sponsors = JSON.parse(localStorage.getItem('advancedSponsors') || '[]');
      this.venues = JSON.parse(localStorage.getItem('advancedVenues') || '[]');
      this.analytics = JSON.parse(localStorage.getItem('advancedAnalytics') || '[]');
    } catch (error) {
      console.error('Error loading from localStorage:', error);
    }
  }

  setupEventListeners() {
    this.createEvent = this.createEvent.bind(this);
    this.createActivity = this.createActivity.bind(this);
    this.switchView = this.switchView.bind(this);
    this.handleFilterChange = this.handleFilterChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.viewEvent = this.viewEvent.bind(this);
    this.editEvent = this.editEvent.bind(this);
  }

